import React from "react";
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Services from './components/pages/Services';
import Nba from './components/Nba';
import Home from './components/pages/Home';
import Signup from "./components/Signup"
import { AuthProvider } from "./contexts/AuthContext"
import NavbarBis from './components/Navbar'
import Dashboard from "./components/Dashboard"
import Login from "./components/Login"
import PrivateRoute from "./components/PrivateRoute"
import ForgotPassword from "./components/ForgotPassword"
import UpdateProfile from "./components/UpdateProfile"
import News from "./components/news/pagenews"
import { NewsContextProvider } from "./components/news/newsContext";


import 'bootstrap/dist/css/bootstrap.min.css';

function App() {
  return(
    <div>
      <Router>
        <NavbarBis />
        <Switch>
          <Route path='/nba-world' exact component={Home} />
          <Route path='/services' component={Services} />
          <Route path='/stat' component={Nba} />
          <NewsContextProvider>
            <Route path='/news' component={News} />
          </NewsContextProvider>


            <div className="w-100" style={{ maxWidth: "400px" }}>
                <AuthProvider>
                    <PrivateRoute exact path="/home" component={Dashboard} />
                    <PrivateRoute path="/update-profile" component={UpdateProfile} />
                    <Route path="/signup" component={Signup} />
                    <Route path="/login" component={Login} />
                    <Route path="/forgot-password" component={ForgotPassword} />
                </AuthProvider>
            </div>
      
        </Switch>
      </Router>
    </div>
  )
}

export default App;
