import React, { Component } from "react";
import axios from "axios";

import Player from "./Player";

class Nba extends Component {

  constructor(props){
    super(props)
    this.state={
      playerName: null,
      playerStats: {}
    }
  }

  handleSubmit = (e) => {
    e.preventDefault();
    this.getPlayerId()
    console.log(this.state.playerName)
  }

  handlechange = (event) => {
    const replace =  event.target.value.split(" ").join("_");
    if (replace.length > 0) {
      this.setState({playerName: replace})
    } else {
      alert("ATTENTION! Le nom du joueur est invalide")
    }
  } 


  getPlayerId = () => {
    axios.get(`https://www.balldontlie.io/api/v1/players?search=${this.state.playerName}`)
    .then(async res => {
      //console.log(res.data.data)
      if (res.data.data[0] === undefined) {
        //alert("ATTENTION! Ce joueur n'a pas encore jouer")
      } else if (res.data.data.length > 1) {
        alert("Le nom est incomplet")
      }else{
        await this.getPlayerStats(res.data.data[0].id)
      }
    }).catch(err => {
      console.log(err)
    })
  }

  getPlayerStats = (playerId) => {
    axios.get(`https://www.balldontlie.io/api/v1/season_averages?season=2020&player_ids[]=${playerId}`)
    .then(async res => {
      console.log(res.data.data)
      this.setState({playerStats: res.data.data[0]})
    }).catch(err => {
      console.log(err)
    })
  }

  

  render(){
    return (
      <div className="App">

          <form className="container" onSubmit={this.handleSubmit}>
            <div class="mb-3 container input-group input-group-lg">
              <input type="text" class="form-control" aria-label="Large" value={this.state.value} onChange={this.handlechange} placeholder="Le nom du joueur"></input>
              <div class="input-group-prepend">
                <input type="button" class="input-group-text" variant="primary" value="recherche" />
              </div> 
            </div>
          </form>

          <table class="table container">
            <thead class="thead-light">
              <tr>
              <th scope="col">Matchs jou√©s </th>
                <th scope="col">Point par match :</th>
                <th scope="col">Rebonds par match :</th>
                <th scope="col">Assistes par match :</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <th>{this.state.playerStats["games_played"]}</th>
                <th>{this.state.playerStats["pts"]}</th>
                <td>{this.state.playerStats["reb"]}</td>
                <td>{this.state.playerStats["ast"]}</td>
              </tr>
            </tbody>
          </table>
          <Player />
        </div>  
    )
  }

}

export default Nba;
